import React, { useEffect, useRef, useState } from "react";
import Box from "@mui/material/Box";
import Paper from "@mui/material/Paper";
import Pagination from "@mui/material/Pagination";
import Stack from "@mui/material/Stack";
import FormGroup from "@mui/material/FormGroup";
import FormControlLabel from "@mui/material/FormControlLabel";
import Radio from "@mui/material/Radio";
import RadioGroup from "@mui/material/RadioGroup";
import TextField from "@mui/material/TextField";
import Checkbox from "@mui/material/Checkbox";
import Button from "@mui/material/Button";
import FormControl from "@mui/material/FormControl";
import FormLabel from "@mui/material/FormLabel";
import FormHelperText from "@mui/material/FormHelperText";
import Alert from "@mui/material/Alert";
import AlertTitle from "@mui/material/AlertTitle";
import Tabs from "@mui/material/Tabs";
import Tab from "@mui/material/Tab";
import Backdrop from "@mui/material/Backdrop";
import AssignmentTurnedInIcon from "@mui/icons-material/AssignmentTurnedIn";
import data from "../data/set1";

const Main = () => {
    const [presentPageData, setPresentPageData] = useState([]);
    const [answers, setAnswers] = useState(
        [...Array(10)].map(() => {
            return {};
        })
    );
    const [errors, setErrors] = useState(
        [...Array(10)].map(() => {
            return false;
        })
    );
    const [currSet, setCurrSet] = useState(0);
    const [currPage, setCurrPage] = useState(0);
    const [submitted, setSubmitted] = useState(false);
    const [result, setResult] = useState(false);

    const scrollRef = useRef();
    const score = useRef(0);

    const totalPages = Math.ceil(
        data.slice(currSet * 60, currSet * 60 + 60).length / 10
    );

    const handleSubmit = (e) => {
        e.preventDefault();
        setSubmitted(true);
        let errors = 0;
        presentPageData.forEach((val, ind) => {
            let errorPresent = false;
            if (val.QUESTION_TYPE === "Single") {
                let ans = [];
                let check = answers[ind];
                for (const property in check) {
                    if (check[property]) ans.push(property);
                }
                ans = ans.join(",");
                if (val.Answer !== ans) errorPresent = true;
            } else if (
                val.QUESTION_TYPE === "TF" &&
                val.Answer !== answers[ind]
            ) {
                errorPresent = true;
            } else if (val.QUESTION_TYPE === "Fill") {
                if (typeof answers[ind] === "object") {
                    errorPresent = true;
                } else if (
                    val.Answer.toLowerCase() !== answers[ind].toLowerCase()
                ) {
                    errorPresent = true;
                }
            }
            if (errorPresent) errors += 1;
            setErrors((prev) => {
                let prepareErr = [...prev];
                prepareErr[ind] = errorPresent;
                return prepareErr;
            });
        });
        setResult(
            `${presentPageData.length - errors} / ${presentPageData.length}`
        );
    };

    const handleOptionChange = (questNo, e, type) => {
        if (type === "Single") {
            setAnswers((prev) => {
                let prepareAns = [...prev];
                prepareAns[questNo] = {
                    ...prepareAns[questNo],
                    ...(prepareAns[questNo][e.target.value] === undefined
                        ? { [e.target.value]: true }
                        : {
                              [e.target.value]:
                                  !prepareAns[questNo][e.target.value],
                          }),
                };
                return prepareAns;
            });
        } else if (type === "TF") {
            setAnswers((prev) => {
                let prepareAns = [...prev];
                prepareAns[questNo] = e.target.value;
                return prepareAns;
            });
        } else if (type === "Fill") {
            setAnswers((prev) => {
                let prepareAns = [...prev];
                prepareAns[questNo] = e.target.value;
                return prepareAns;
            });
        }
    };

    const handlePageChange = (page) => {
        setCurrPage(page);
        scrollRef.current.scrollIntoView({ behavior: "smooth" });
    };

    useEffect(() => {
        setSubmitted(false);
        setAnswers(
            [...Array(10)].map(() => {
                return {};
            })
        );
        setErrors(
            [...Array(10)].map(() => {
                return false;
            })
        );
        const dataToSet = data
            .slice(currSet * 60, currSet * 60 + 60)
            .slice(currPage * 10, currPage * 10 + 10);
        setPresentPageData(dataToSet);
    }, [currSet, currPage]);

    return (
        <>
            <Backdrop
                sx={{
                    color: "#fff",
                    zIndex: (theme) => theme.zIndex.drawer + 1,
                }}
                open={result}
                onClick={() => setResult(false)}
            >
                <Alert onClose={() => setResult(false)} severity="info">
                    Correct Answers: {result}
                </Alert>
            </Backdrop>
            <Box sx={{ width: "100%", mb: "15px" }} ref={scrollRef}>
                <Tabs
                    value={currSet}
                    onChange={(e, v) => {
                        setCurrSet(v);
                        setCurrPage(0);
                    }}
                    textColor="secondary"
                    indicatorColor="secondary"
                    variant="scrollable"
                    scrollButtons="auto"
                    aria-label="secondary tabs example"
                >
                    {[...Array(Math.ceil(data.length / 60))].map((v, i) => (
                        <Tab key={i} value={i} label={`Set ${i + 1}`} />
                    ))}
                </Tabs>
            </Box>
            <Box sx={{ mb: "20px" }}>
                <form onSubmit={handleSubmit}>
                    {presentPageData.map((val, ind) => (
                        <Paper
                            key={ind}
                            elevation={3}
                            sx={{
                                pt: "5px",
                                pl: "20px",
                                pr: "20px",
                                pb: "20px",
                                mb: "20px",
                            }}
                        >
                            <FormControl
                                error={submitted && errors[ind]}
                                component="fieldset"
                                variant="standard"
                            >
                                <FormGroup>
                                    {val.QUESTION_TYPE === "Single" && (
                                        <div>
                                            <FormLabel id="demo-error-radios">
                                                Q{currPage * 10 + ind + 1}.{" "}
                                                {
                                                    val.QUESTION_TEXT.split(
                                                        /(\d+[.)])/
                                                    )[0]
                                                }
                                            </FormLabel>
                                            {val.QUESTION_TEXT.split(
                                                /(\d+[.)])/
                                            ).map((v, i) => (
                                                <div key={`${v}-${i}`}>
                                                    {i !== 0 && i % 2 === 0 && (
                                                        <FormControlLabel
                                                            control={
                                                                <Checkbox
                                                                    onChange={(
                                                                        e
                                                                    ) =>
                                                                        handleOptionChange(
                                                                            ind,
                                                                            e,
                                                                            val.QUESTION_TYPE
                                                                        )
                                                                    }
                                                                    value={
                                                                        i / 2
                                                                    }
                                                                />
                                                            }
                                                            label={v}
                                                        />
                                                    )}
                                                </div>
                                            ))}
                                        </div>
                                    )}
                                    {val.QUESTION_TYPE === "TF" && (
                                        <div
                                            key={`${val.QUESTION_TEXT}-${ind}`}
                                        >
                                            <FormLabel id="demo-error-radios">
                                                Q{ind + 1}. {val.QUESTION_TEXT}
                                            </FormLabel>
                                            <RadioGroup
                                                aria-labelledby="demo-radio-buttons-group-label"
                                                name="radio-buttons-group"
                                                onChange={(e) =>
                                                    handleOptionChange(
                                                        ind,
                                                        e,
                                                        val.QUESTION_TYPE
                                                    )
                                                }
                                            >
                                                <FormControlLabel
                                                    value="TRUE"
                                                    control={<Radio />}
                                                    label="True"
                                                />
                                                <FormControlLabel
                                                    value="FALSE"
                                                    control={<Radio />}
                                                    label="False"
                                                />
                                            </RadioGroup>
                                        </div>
                                    )}
                                    {val.QUESTION_TYPE === "Fill" && (
                                        <div key={`${val}-${ind}`}>
                                            <FormLabel id="demo-error-radios">
                                                Q{ind + 1}. {val.QUESTION_TEXT}
                                            </FormLabel>
                                            <br />
                                            <TextField
                                                label="Type here"
                                                error={submitted && errors[ind]}
                                                variant="standard"
                                                sx={{ my: "15px" }}
                                                onChange={(e) =>
                                                    handleOptionChange(
                                                        ind,
                                                        e,
                                                        val.QUESTION_TYPE
                                                    )
                                                }
                                            />
                                        </div>
                                    )}
                                </FormGroup>
                                {submitted && (
                                    <>
                                        {errors[ind] ? (
                                            <Alert severity="error">
                                                <AlertTitle>Oops!</AlertTitle>
                                                Correct Answers: {val.Answer}
                                            </Alert>
                                        ) : (
                                            <Alert severity="success">
                                                <AlertTitle>Hurray!</AlertTitle>
                                                You have selected correct
                                                answers
                                            </Alert>
                                        )}
                                        <FormHelperText>
                                            {val.explanation}
                                        </FormHelperText>
                                    </>
                                )}
                            </FormControl>
                        </Paper>
                    ))}
                    <Box
                        sx={{
                            display: "flex",
                            justifyContent: "space-between",
                        }}
                    >
                        <Stack mt={2} spacing={2}>
                            <Pagination
                                count={totalPages}
                                page={currPage + 1}
                                onChange={(e, v) => handlePageChange(v - 1)}
                                color="secondary"
                            />
                        </Stack>
                        <Button
                            variant="contained"
                            endIcon={<AssignmentTurnedInIcon />}
                            type="submit"
                        >
                            Verify
                        </Button>
                    </Box>
                </form>
            </Box>
        </>
    );
};

export default Main;
