import SignInSide from "./components/auth";
import { MemoryRouter, Routes, Route } from "react-router-dom";
import Main from "./components/testTaker/1";
import Selector from "./components/selector";
import Main2 from "./components/testTaker/2";
import Base from "./components/base";

function App() {
    return (
        <MemoryRouter initialEntries={["/"]}>
            <Base>
                <Routes>
                    <Route path="/" element={<SignInSide />}></Route>
                    <Route path="/main" element={<Selector />}></Route>
                    <Route path="/1" element={<Main />}></Route>
                    <Route path="/2" element={<Main2 />}></Route>
                </Routes>
            </Base>
        </MemoryRouter>
    );
}

export default App;
