import { Container } from "@mui/material";
import React from "react";
import ResponsiveAppBar from "./topBar";

const Base = ({ children }) => {
    return (
        <Container maxWidth="lg" sx={{ p: { xs: "0px" }, mb: 4 }}>
            <ResponsiveAppBar />
            {children}
        </Container>
    );
};

export default Base;
